.resume__sidebar {
    grid-area: sidebar;
    background-color: $sidebar;
}

.profile{
    display: grid;
    padding: rem(10);
    background-color: darken($sidebar, 10);
    justify-items: center;
    &__image{
        margin-bottom: rem(20);
    }
    &__name{
        color: $red;
    }
    &__title{
        color: $red;
        font-weight: 400;
    }
}